export default {
	data () {
		return {
			money: '',
			// 用于余额
			userBalance: 168.05
		}
	},
	methods: {
		openProcess() {
			console.log('提交')
			console.log('当前金额=> ' + this.money)
			
			// 发送请求,返回结算成功跳转到提现成功页
			// uni.navigateTo({
			// 	url: '../../pages/me_cash_withdrawal_process/me_cash_withdrawal_process',
			// 	complete (res) {
			// 		console.log(res)
			// 	}
			// })
		},
		onChange(e) {
			// console.log(e.detail.value)
			let newValue = e.detail.value
			// console.log(newValue)
			// 不能让金额大于用户余额
			
			// 若等于
			if(newValue == '00') {
				console.log('两个0开头')
				// 这里v-model有个BUG，需要设置延迟才行
				if(st) {
					clearTimeout(st)
				}
				let st = setTimeout(() => { this.money = '' }, 5)
				console.log(this.money)
			}
			
			if(newValue == '0.00') {
				console.log('都是0')
				// 这里v-model有个BUG，需要设置延迟才行
				if(st) {
					clearTimeout(st)
				}
				let st = setTimeout(() => { this.money = '' }, 5)
				console.log(this.money)
			}
			
			
			if(Number(newValue)>Number(this.userBalance)) {
				console.log('金额大于了')
				// 这里v-model有个BUG，需要设置延迟才行
				if(st) {
					clearTimeout(st)
				}
				let st = setTimeout(() => { this.money = this.userBalance }, 5)
				console.log(this.money)
			}
			
			
			if(isNaN(Number(newValue))) {
				console.log('不是数字')
				if(st) {
					clearTimeout(st)
				}
				let st = setTimeout(() => { this.money = '' }, 0)
			} 
			
			if((Number(newValue))<0){
				// 是数字，但必须大于0
				console.log('小于0')
				if(st) {
					clearTimeout(st)
				}
				let st = setTimeout(() => { this.money = '' }, 0)
			} 
			
			
			if(String(newValue).indexOf(" ")>=0) {
				console.log('有空格')
				if(st) {
					clearTimeout(st)
				}
				let st = setTimeout(() => { this.money = '' }, 0)
			}
			
			
			// 保留两位小数
			if(String(newValue).indexOf(".")>=0) {
				console.log('有小数点')
				this.ifTwo(newValue)
			}
			
			
			
		},
		// 保留两位小数
		ifTwo(num) {
			// 如果大于两位小数，那么就保留两位小数
			let floatNum = (String(num).split(".")[1].length)
			if(floatNum>2) {
				let result = 0
				console.log('小数点后，保留两位')
				result = Number(num).toFixed(2);
				this.money = 0
				if(st) {
					clearTimeout(st)
				}
				let st = setTimeout(() => { 
					this.money = result
					
					// 然后再次判断是否大于用户余额
					if(Number(this.money)>Number(this.userBalance)) {
						console.log('金额大于了')
						// 这里v-model有个BUG，需要设置延迟才行
						if(stdd) {
							clearTimeout(stdd)
						}
						let stdd = setTimeout(() => { this.money = this.userBalance }, 25)
						console.log(this.money)
					}
					
				}, 20)
			}
		}
	}
}
